.page6>main {
    margin: 1rem;
    border: solid 1px var(--divider-color);
    border-radius: 5px;
    background-color: var(--paper-color);
    padding: 0rem !important;
    display: flex;
    flex-direction: column;
}

.page6>aside {
    margin: 1rem;
    border: solid 1px var(--divider-color);
    border-radius: 5px;
    background-color: var(--paper-color);
    padding: 0rem !important;
    display: flex;
    flex-direction: column;
}
